import Vue from "vue";
import Router from "vue-router";

const Demo_table = () => import("@/components/demo/table");
const Demo_layout = () => import("@/components/demo/layout");
const Demo_vuex = () => import("@/components/demo/vuex");
const InterDetail = () => import("@/components/inter/InterDetail");
const InterEdit = () => import("@/components/inter/InterEdit");
const Card = () => import("@/components/inter/Card");
const inter_Add = () => import("@/components/inter/interAdd");
const interLog = () => import("@/components/interLog/interLog");
const Demo_request = () => import("@/components/demo/demoreq");
const interList = () => import("@/components/inter/interlist");
const mockHome = () => import("@/components/home/index");
const intro = () => import("@/components/intro");
const system = () => import("@/components/system/system");
const fileUpload = () => import("@/components/inter/fileupload");
Vue.use(Router);

const router = new Router({
  mode: "hash",
  base: "/mockweb/",
  routes: [
    {
      path: "/system/system",
      component: system,
    },
    {
      path: "/",
      name: "mockHome",
      component: mockHome,
    },
    {
      path: "/intro",
      name: "intro",
      component: intro,
    },
    {
      path: "/inter",
      component: interList,
    },
    {
      path: "/fileUpload",
      component: fileUpload,
    },
    {
      path: "/Demo_table",
      component: Demo_table,
    },
    {
      path: "/Demo_layout",
      component: Demo_layout,
    },
    {
      path: "/interLog",
      name: "interLog",
      component: interLog,
    },
    {
      path: "/Demo_vuex",
      component: Demo_vuex,
    },
    {
      path: "/InterDetail",
      component: InterDetail,
    },
    {
      path: "/InterEdit",
      component: InterEdit,
    },
    {
      path: "/Demo_request",
      component: Demo_request,
    },
    {
      path: "/interAdd",
      component: inter_Add,
    },
    {
      path: "/Card",
      component: Card,
    },
  ],
});

export default router;



