import { Notification, Loading,MessageBox} from "element-ui";

//element-ui/Message-消息提示封装
export function MSG(msg, flag,pos) {
    //top-right/top-left/bottom-right/bottom-left
    pos=(pos==0?"top-right":(pos==1?"top-left":(pos==2?"bottom-right":"bottom-left")));

    Notification({
        duration: flag == "error" ? 5000 : 3000,
        message: msg,
        type: flag,
        position:pos,
        offset:80
    });

}

//element-ui/load-加载提示封装
export function LOAD(el,msg) {

    const LOAD = Loading.service({
        target: el ? el : document.body,
        lock:true,
        text: msg ? msg:'加载中...'
    });

    setTimeout(() => {
        //3分钟延时自动关闭
        LOAD.close();
    }, 180000);

    return LOAD
}

//element-ui/MessageBox-加载提示封装
export function BOX(msg,flag,backFunc) {
    MessageBox(msg, '提示', {
        confirmButtonText: '确定',
        type:flag,
        callback:backFunc()
    }); 
}


